import React, { ComponentProps } from 'react';
import { Button, Box } from '@mui/material';
import Emoji from 'components/Utils/Emoji';
import { useSandboxApply } from 'src/hooks/useSandboxApply';
import { SandboxApplyStatus } from 'src/models/sandbox';

const descriptionStyle = {
  fontSize: 'subtitle1.fontSize',
  marginRight: '2px',
  color: 'primary.main',
};

interface Props extends ComponentProps<typeof Button> {
  expiredText?: string;
}
const CountDown = ({ expiredText = '마감되었습니다' }: Props) => {
  const { count, status } = useSandboxApply();

  return (
    <div>
      {status === SandboxApplyStatus.마감 ? (
        expiredText
      ) : (
        <Box component="span" sx={{ fontSize: 'body2.fontSize' }}>
          <Box component="strong" sx={descriptionStyle}>
            {count.days}
          </Box>
          일{' '}
          <Box component="strong" sx={descriptionStyle}>
            {count.hours}
          </Box>
          시간{' '}
          <Box component="span" sx={descriptionStyle}>
            {count.minutes}
          </Box>
          분{' '}
          <Box component="span" sx={descriptionStyle}>
            {count.seconds}
          </Box>
          초 후에 마감됩니다 <Emoji emoji="🙇‍♂️" name="thanks" />
        </Box>
      )}
    </div>
  );
};

export default CountDown;
